import * as React from 'react';
import { Button } from 'antd';
import './BalanceTest.css'

export class BalanceTest extends React.Component {
  state = {
    event: {},
    data: [],
    isRecording: false,
    startButton: true,
    buttonMessage: 'Start!'
  }
  componentDidMount() {
    if(window.DeviceOrientationEvent){
      window.addEventListener("deviceorientation", this.orientation, false);
    }else{
      console.log("DeviceOrientationEvent is not supported");
    }
  }

  componentWillUnmount() {
    if(window.DeviceOrientationEvent){
      window.removeEventListener("deviceorientation", this.orientation, false);
    }else{
      console.log("DeviceOrientationEvent is not supported");
    }
  }

  render() {
    const {beta, gamma} = this.state.event
    return (
      <div className='stack'>
        <div className='instructions'>
          <p className='instruction'><span>Step 1:</span> Stand on your left leg.</p>
          <p className='instruction'><span>Step 2: </span>Hold your phone in your dominant hand.</p>
          <p className='instruction'><span>Step 3: </span> Extend arms out to the side with palms down.</p>
          <p className='instruction'><span>Step 4: </span> Wait for completion sound.</p>
          <p className='instruction'>When you are ready, please press the Start button to begin the Balance test.</p>
          {!this.state.isRecording && <Button disabled={!this.state.startButton} type="primary" icon="play" onClick={this.startRecording}>
            {this.state.buttonMessage}
          </Button>}
        </div>
        <div>
        <img alt='' width='100px' src='balance.svg' style={{ margin: '1rem', transform: `translate3d(${(gamma/180)*50}px, ${(Math.abs(beta)/180)*50}px, 0)` }}/>
        </div>
      </div>
    )
  }

  orientation = (event) => {
    const {alpha, beta, gamma} = event
    const {addData} = this.props
    if (this.state.isRecording) {
      addData({alpha, beta, gamma})
    }
    this.setState({event: {alpha, beta, gamma}})
  }

  startRecording = () => {
    this.setState({startButton: false})
    setTimeout(() => this.setState({buttonMessage: 'Game Starting in 3...'}), 0)
    setTimeout(() => this.setState({buttonMessage: 'Game Starting in 2...'}), 1000)
    setTimeout(() => this.setState({buttonMessage: 'Game Starting in 1...'}), 2000)
    setTimeout(() => this.setState({buttonMessage: 'Information recorded!'}), 3000)
    setTimeout(() => this.setState({isRecording: true}), 3000)
    setTimeout(this.props.toggleDone, 13000)
    setTimeout(beep, 13000)
    setTimeout(beep, 13100)
    setTimeout(beep, 13200)

    setTimeout(() => this.setState({isRecording: false}), 13000)
  }
}

function beep() {
  var snd = new  Audio("data:audio/wav;base64,");
  snd.play();
}


export default BalanceTest
